.pdf-embed {
  width: 100%;
  height: 100%;
  min-height: 500px;
  border: none;
  display: block;
}

/* IFRAME - fallback */
.pdf-iframe {
  width: 100%;
  height: 100%;
  min-height: 500px;
  border: none;
}

/* iOS   */
@supports (-webkit-touch-callout: none) {
  .pdf-viewer {
    /*   */
    overflow-y: auto;
