  name: 'AppSlider',

  props: {
    //  
    modelValue: {
      type: Number,
      required: true,
    },
    min: {
      type: Number,
      default: 0,
    },
    max: {
      type: Number,
      default: 100,
    },
    step: {
      type: Number,
      default: 1,
    },

    //  
    showTooltip: {
      type: Boolean,
      default: true,
    },
    showMarks: {
      type: Boolean,
      default: false,
    },
    showInput: {
      type: Boolean,
      default: false,
    },
    marks: {
      type: Array as () => Mark[],
      default: () => [],
    },

    // 
    suffix: {
      type: String,
      default: '',
    },
    format: {
