      if (!sliderRect) return;

      const offsetX = event.clientX - sliderRect.left;
      const width = sliderRect.width;

      let percentage = offsetX / width;
      if (percentage < 0) percentage = 0;
      if (percentage > 1) percentage = 1;

      let newValue = props.min + percentage * (props.max - props.min);

      //   step
      const steps = Math.round((newValue - props.min) / props.step);
      newValue = props.min + steps * props.step;

      updateValue(newValue);
    };

    //    
    const handleMarkClick = (value: number): void => {
      if (props.disabled) return;
      updateValue(value);
    };

    //  
    const handleKeydown = (event: KeyboardEvent): void => {
      if (props.disabled) return;

      let newValue = normalizedValue.value;

      switch (event.key) {
        case 'ArrowLeft':
        case 'ArrowDown':
          newValue -= props.step;
          event.preventDefault();
          break;
        case 'ArrowRight':
        case 'ArrowUp':
          newValue += props.step;
          event.preventDefault();
          break;
        case 'Home':
          newValue = props.min;
          event.preventDefault();
          break;
        case 'End':
          newValue = props.max;
          event.preventDefault();
          break;
        case 'PageDown':
          newValue -= props.step * 10;
          event.preventDefault();
          break;
        case 'PageUp':
          newValue += props.step * 10;
          event.preventDefault();
          break;
        default:
          return;
      }

      //  
      newValue = Math.max(props.min, Math.min(props.max, newValue));

      updateValue(newValue);
    };

    //    input
    const handleInputChange = (): void => {
      let value = parseFloat(String(inputValue.value));

      if (isNaN(value)) {
        inputValue.value = normalizedValue.value;
        return;
      }

      //  
      value = Math.max(props.min, Math.min(props.max, value));

      //   step
      const steps = Math.round((value - props.min) / props.step);
      value = props.min + steps * props.step;

      updateValue(value);
      inputValue.value = value;
    };

    //  
    const updateValue = (newValue: number): void => {
      const oldValue = normalizedValue.value;

      if (newValue !== oldValue && !props.disabled) {
        emit('update:modelValue', newValue);
        emit('change', newValue, oldValue);
        emit('input', newValue);
        inputValue.value = newValue;
      }
    };

    //  inputValue  modelValue
    watch(
      () => props.modelValue,
      (newValue: number) => {
        inputValue.value = newValue;
      },
      { immediate: true }
    );

    //    
    onUnmounted(() => {
      stopDrag();
    });

    //         
    return {
      // refs
      sliderThumb,
      inputValue,

      // computed
      normalizedValue,

      // methods
      formatValue,
      startDrag,
      handleSliderClick,
      handleMarkClick,
      handleKeydown,
      handleInputChange,

      // props    
      props,
      focusThumb,
    };
  },
});

